import React, { useEffect } from 'react';

import './Sidebar.css';

// IMPORT ICONS
import home from '../../../assets/icons/home.svg';
import home1 from '../../../assets/icons/home1.svg';

import studentManageIco from '../../../assets/icons/bookmark.svg';

import feesManageIco from '../../../assets/icons/dollar-sign.svg';

import feesIco from '../../../assets/icons/file-text.svg';

import studentEntryIco from '../../../assets/icons/type.svg';
import studentEntryIco1 from '../../../assets/icons/type (1).svg';

import monthlyFeeProcessIco from '../../../assets/icons/activity.svg';
import monthlyFeeProcessIco1 from '../../../assets/icons/activity (1).svg';

import feeCollectionIco from '../../../assets/icons/file-plus.svg';
import feeCollectionIco1 from '../../../assets/icons/file-plus (1).svg';

import studentFormIco from '../../../assets/icons/align-center.svg';
import studentFormIco1 from '../../../assets/icons/align-center (1).svg';

import studentListIco from '../../../assets/icons/list.svg';
import studentListIco1 from '../../../assets/icons/list (1).svg';

import outstandingFeeIco from '../../../assets/icons/award.svg';
import outstandingFeeIco1 from '../../../assets/icons/award (1).svg';

// IMPORT LINK FROM LINK COMPONENT
import Links from './Links/Links';

// IMPORT JQUERY
import $ from 'jquery';

const Sidebar = () => {

    useEffect(
        () => {

            $('.DropDowns').slideUp(0);

            $('.SideBar a').on(
                'click', () => {

                    openMenu();
                    
                }
            )

        }, []
    );

    // TO OPEN SIDEBAR
    const openMenu = () => {

        $('.SideBar').toggleClass('showSidebar');
        $('.Topbar .la-times').addClass('la-ellipsis-v');
        $('.Topbar .la-ellipsis-v').removeClass('la-times');

    }

    return (
        <div className="SideBar">

            {/* CROSS ICON TO CLOSE THE SIDEBAR ON 600 SCREEN */}
            <i className="las la-times" onClick={ openMenu }></i>

            <Links 
                icon={ home1 } 
                activeIcon={ home } 
                txt="Home" 
                link="/dashboard"
             />
            <Links 
                icon={ studentManageIco } 
                txt="Student Management" 
                dropdown={ true }
                openId='StudentManagementDropdown'
             />
             <div id="StudentManagementDropdown" className="DropDowns">
                <Links 
                    icon={ studentEntryIco } 
                    activeIcon={ studentEntryIco1 } 
                    txt="Student Entry" 
                    link="/students/entry=new"
                />
             </div>
             <Links 
                icon={ feesManageIco } 
                txt="Fees Management" 
                dropdown={ true }
                openId='FeesManagementDropdown'
             />
             <div id="FeesManagementDropdown" className="DropDowns">
                <Links 
                    icon={ monthlyFeeProcessIco } 
                    activeIcon={ monthlyFeeProcessIco1 } 
                    txt="Monthly Fee Process" 
                    link="/management/fees/process"
                />
                <Links 
                    icon={ feeCollectionIco } 
                    activeIcon={ feeCollectionIco1 } 
                    txt="Fee Collection" 
                    link="/management/fees/process"
                />
             </div>
             <Links 
                icon={ feesIco } 
                txt="Reports" 
                dropdown={ true }
                openId='StudentReportsDropdown'
             />
             <div id="StudentReportsDropdown" className="DropDowns">
                <Links 
                    icon={ studentFormIco } 
                    activeIcon={ studentFormIco1 } 
                    txt="Student Form" 
                    link="/management/fees/process"
                />
                <Links 
                    icon={ studentListIco } 
                    activeIcon={ studentListIco1 } 
                    txt="Student List" 
                    link="/students/list"
                />
                <Links 
                    icon={ outstandingFeeIco } 
                    activeIcon={ outstandingFeeIco1 } 
                    txt="Outstanding Fee" 
                    link="/management/fees/process"
                />
             </div>
        </div>
    )
}

export default Sidebar
