import React, { useState } from 'react';

import './Links.css';

// REACT ROUTING PACKAGES
import { NavLink } from 'react-router-dom';

// ICONS
import DownIco from '../../../../assets/icons/chevron-down.svg';

// IMPORT JQUERY
import $ from 'jquery';

const Links = ( props ) => {

    const [ ActiveLink, setActiveLink ] = useState('');

    const OpenDropdown = ( id ) => {

        $('.DropDowns').slideUp('slow').not('#' + id);
        $('#' + id).slideToggle('slow');

    }

    return (
        <>
            {
                props.dropdown
                ?
                <div className="link" onClick={ () => OpenDropdown(props.openId) }>
                    <img src={ props.icon } width='20' height="20" alt='home' />
                    <p className="mb-0 ml-2">{ props.txt }</p>
                    <img src={ DownIco } width='20' height="20" className="ml-auto" alt='dropdown' />
                </div>
                :
                <NavLink 
                            to={ props.link } 
                            activeClassName="activeLink" 
                            className="link" 
                            isActive={
                                (match) => {
                                    match && setActiveLink(props.link); // <-- set active index
                                    return match; // <-- return boolean
                                }
                            }
                >
                            {
                                ActiveLink === props.link
                                ?
                                <img src={ props.activeIcon } width='20' height="20" alt='home' />
                                :
                                <img src={ props.icon } width='20' height="20" alt='home' />
                            }
                            <p className="mb-0 ml-2">{ props.txt }</p>
                </NavLink>
            }
        </>
    );
}

export default Links;